<template>
  <div id="PianoMemory" class="pb-16">
    
    <!-- <Alert ref="Alert"/> -->

    
    <!-- <InteractiveBord @changeSetting="changeSettings()" :score="score" :haveTimeLeft="false" :maxW="'max-w-xl'" ref="InteractiveBord"/> -->


    <div class="w-full flex flex-col items-center font-bold">
      <div class="w-full flex flex-col max-w-xl mt-3 px-5 text-2xl">
        
        <!-- <div v-show="stopClick" class="w-full flex justify-center px-3">
          <div class="relative w-4/5 flex rounded-lg overflow-hidden">
            <span class="w-full pt-3 bg-green-500"></span>
            <span :style="'width:'+timeRunFlashList" class="absolute transition pt-3 bg-red-500"></span>
          </div>
        </div> -->

        <div class="w-full flex flex-wrap" :class="stopClick ? '':'mt-3'">
          <div class="w-1/3 mt-4 px-2 h-32" v-for="(item,index) in 9" :key="index" >
            <div class="relative w-full h-full rounded-lg overflow-hidden" :class="{'flash':item.isFlash}" @click="isclick(index)">
              <div class="mainView bg-red-400 w-full h-full items-center text-center" :class="{'cursor-pointer':!stopClick}"/>
              <div class="overlayPiano inset-0 px-3 bg-purple-900 w-full h-full" :class="{'cursor-pointer':!stopClick}" />
            </div>
          </div>
        </div>
        
      </div>
    </div>
    
    <!-- <LevelsSelect @dataForApp="updateLevelValue" /> -->
    
  </div>
</template>

<script>
/* import Alert from '../components/Alert' */
/* import InteractiveBord from '../components/InteractiveBord' */
/* import LevelsSelect from '../components/LevelsSelect' */
export default {
  name: 'PianoMemory',
  components: {
    /* Alert, */
    /* InteractiveBord, */
    /* LevelsSelect */
  },
  data() {
    return {
      items:[],
      flashList:[],
      userClick:0,

      stopClick:true,
      stopGame:false,

      timeRunFlashList:"100%",

      score:0,

      level:0,
      gameOverSound:require("../assets/sounds/GameOver.ogg")
      
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      clearInterval(this.musab)
      this.addItems();
      this.musab = setTimeout(() => {
        this.addNumberToFlashList();
      }, 1000);
    },
    isclick(id){
      if(this.stopClick)return;
      if(this.stopGame)return;

      var alert = this.$refs.Alert;
      this.addFlashClass(id,300)
      if(id != this.flashList[this.userClick]){
        this.stopGame = true;
        (new Audio(this.gameOverSound)).play();
        alert.alertShow("you Loss","bg-red-500",7000)
        console.log("you Loss")
        return
      }

      this.userClick++;

      if(this.userClick == this.flashList.length){
        this.userClick = 0;
        this.stopClick = true;
        setTimeout(() => {
          this.addNumberToFlashList();
        }, 1500);
        this.score += 1;
        alert.alertShow("+1","bg-green-500",200)
        console.log("this Level complet")
      }
    },
    addItems(){
      var vm=this
      var num = 0;

      vm.items = [];
      if(vm.level == 0)num = 4;
      else if(vm.level == 1)num = 9;
      else if(vm.level == 2)num = 16;
      for (let i = 0; i < num; i++) {
        vm.items.push({
          isFlash:false
        })        
      }
    },
    addNumberToFlashList(){
      var num = Math.floor(Math.random() * this.items.length)
      this.flashList.push(num)

      this.runFlashList();
    },
    runFlashList(id = 0){
      if (id > this.flashList.length) {
        console.log("done")
        return
      }

      this.addFlashClass(this.flashList[id],800)
      this.updateTimeRunFlashList(id)
    
      setTimeout(() => {
        this.runFlashList(id + 1)
      }, 1200);
    },

    addFlashClass(id,time){
      clearTimeout(this.flashTimeOut)
      var item = this.items[id];
      if(!item)return;

      this.items.forEach(item => {
        item.isFlash = false
      });

      item.isFlash = true;

      this.flashTimeOut = setTimeout(() => {
        item.isFlash = false
      }, time);
    },


    /* updateTimeRunFlashList(id){
      var time = Math.floor((id / this.flashList.length)*100)
      time = 100 - time
      this.timeRunFlashList = time+"%"
      if (!time){
        this.timeRunFlashList = "100%"
        this.stopClick = false;
      }
    }, */

    /* updateLevelValue(value){
      this.level = value;
      this.changeSettings();
    }, */

    /* changeSettings(){
      var vm = this;
      var level = vm.level;
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.level = level;
      vm.MStart();
    } */
  },
  computed: {
    
  },
}
</script>

<style>
.overlayPiano {
  position: absolute;
  opacity: 0;
  transition: .8s ease;
}

.transition{
  transition: 1s ease;
}

.flash .overlayPiano{
  opacity: .95;
}
</style>
